body{
  scroll-behavior: smooth;
}
@import url(https://fonts.googleapis.com/css?family=Didact+Gothic|Comfortaa:400,700&subset=latin,cyrillic);
   @font-face{
        font-family: 'BebasNeueBold';  
        src: url('../font/9114.otf');
  
        }



.App__3st-slide{
height: 120vh;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;         
  -ms-flex-direction: row;         
  flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align:start;
      align-items:flex-start;
  position: relative;
  -webkit-perspective-origin: bottom;
          perspective-origin: bottom;
  z-index: -1;
  overflow: hidden;
}
.App__3st-slide::before{
    position: absolute;
    width: 0;
    height: 0;
    border-style: solid;
    top: -109px;
    border-width: 0 0 110px 1920px;
    border-color: transparent transparent #111;
}

.App__3st-slide video{
min-width: 100%;
	min-height: 100%;
	width: auto;
	height: auto;
position: absolute;
   z-index:-100;
opacity: 0.95;
overflow: hidden;

}
.App__3st-slide_skills{
height: 100%;
 width: 25%; 
display: -ms-flexbox; 
display: flex;
-ms-flex-align: center;
    align-items: center;
-ms-flex-pack: distribute;
    justify-content: space-around;
-ms-flex-direction: column;
flex-direction: column;
   z-index:-1;
}

.App__3st-slide_skill{
margin-top: 5%;
height: auto;
width: 150px;
border-radius: 30%;
-webkit-animation: App__3st-slide_skill 2s forwards linear alternate;
        animation: App__3st-slide_skill 2s forwards linear alternate;
opacity: 0;
border: 1px solid rgb(6, 143, 125);
display: -ms-flexbox;
display: flex;
-ms-flex-pack: center;
    justify-content: center;
-ms-flex-align: center;
    align-items: center;

}
.App__3st-slide-photo{
width: 75%;
height: 100%;
 display: -ms-flexbox;
 display: flex;
 -ms-flex-pack: justify;
     justify-content: space-between;
 -ms-flex-align: center;
     align-items: center;
 -ms-flex-direction: column;
 flex-direction: column; 
   font-family: 'Comfortaa', cursive;
   color: rgb(220, 250, 237)   
}
.App__3st-slide-photo-tree{
width: 80%;
height: 40%;  
margin-left: -10%;
margin-top: -5%;
}
.App__3st-slide-AboutSkillContainer{
 display: -ms-flexbox;
 display: flex; 
 -ms-flex-pack: start; 
     justify-content: flex-start;
 -ms-flex-align: center;
     align-items: center;
 -ms-flex-direction: column;
 flex-direction: column;
 width: 30%;
 border-radius:  15px;
 margin-bottom: 5%;
 margin-top: 10%;
}
.App__3st-slide-AboutSkillContainerMain{
 display: -ms-flexbox;
 display: flex; 
 -ms-flex-pack: justify; 
     justify-content: space-between;
 -ms-flex-align: center;
     align-items: center;
  width: 100%;
  height: 100%;
text-align: center;
}
.App__3st-slide-AboutSkillContainer:last-child {
    margin-top: 12%;
   
}

.App__3st-slide-picture{
width: 50%;
height: 50%; 

 border-radius: 15px;
 
}

.App__3st-slide_skill-text{
margin-top: 5%;
display: block;
  -webkit-animation: App__3st-slide_skill-text 2s forwards linear alternate;
          animation: App__3st-slide_skill-text 2s forwards linear alternate;
color: rgb(194, 248, 221);
}
.App__3st-slide_skill--HTML5{  
  -webkit-animation-delay: 1s;
  animation-delay: 1s;

}
.App__3st-slide_skill--CSS3{
  -webkit-animation-delay: 1.5s;
  animation-delay: 1.5s;
}
.App__3st-slide_skill--JS{
  -webkit-animation-delay: 2s;
  animation-delay: 2s;
}
.App__3st-slide_skill--NPM{
  -webkit-animation-delay: 2.5s;
  animation-delay: 2.5s;
}
.App__3st-slide_skill--WebPack{
  -webkit-animation-delay: 3s;
  animation-delay: 3s;
}
.App__3st-slide_skill--ReactJs{
  -webkit-animation-delay: 3.5s;
  animation-delay: 3.5s;
}
.App__3st-slide_skill--Babel{
  -webkit-animation-delay: 4s;
  animation-delay: 4s; 
}
.App__3st-slide_skill--Github{
  -webkit-animation-delay: 4.5s;
  animation-delay: 4.5s;
}

@media screen and (max-width: 100vw) {
 .App__3st-slide_skill-text{
        font-size: calc(18px + (18 - 14) * ( (100vw - 480px) / ( 1024 - 480) ));
  } 
}

@media screen and (max-width: 850px) {

.App__3st-slide-photo{
   color: rgb(32, 17, 17)   
}
.App__3st-slide{
  background-position: center bottom;  
}
}

@media screen and (max-width: 465px) {
.App__3st-slide-picture{
margin-top: -5%; 
}
}

@media screen and (max-width: 770px) {
  video{
    display: none;
  }
}
@media screen and (max-width: 620px) {
  .App__4st-slide__tree_container {
   width: 300px;
}



.App__3st-slide{
-ms-flex-direction: column;
flex-direction: column;
height: 160vh;
background: -webkit-linear-gradient( rgba(248, 248, 248, 0.466),rgba(0,0,0,.3));
font-size: 20px;
}
.App__3st-slide_skills{
height: 35%;
 width: 99%; 
-ms-flex-pack: center ; 
    justify-content: center ;
}
.App__3st-slide-photo{
width: 100%;
height: 80%;
 font-family: 'Comfortaa', cursive;
 font-size: 0.55em;
 color: rgb(5, 5, 5);

}
  .App__3st-slide_skill{
margin-top: 1%;
 width: 30%; 
}
.App__3st-slide_skill-text {
color: black;
}
.App__3st-slide-picture{
width: 50%;
height: 80%; 
}
.App__3st-slide-photo-tree{
height: 20%;  
}
.App__3st-slide-AboutSkillContainer{
 display: -ms-flexbox;
 display: flex; 
 -ms-flex-pack: justify; 
     justify-content: space-between;
 -ms-flex-align: center;
     align-items: center;
 -ms-flex-direction: column;
 flex-direction: column;
 width: 80%;
 height: 100%;
 margin-top: 0%;
}
.App__3st-slide-AboutSkillContainerMain{
-ms-flex-direction: column;
flex-direction: column; 
}


}

@media screen and (max-width: 100vw) {
    .App__3st-slide-AboutSkillContainer{
        font-size: calc(12px + (18 - 14) * ( (100vw - 480px) / ( 1024 - 480) ));
  } 
}
@media (min-aspect-ratio: 16/9) {
	.App__3st-slide video {
		height: 300%;
		top: -100%;
	}
}
@media (max-aspect-ratio: 16/9) {
	.App__3st-slide video {
		width: 300%;
		left: -100%;
	}
}
@supports ((-o-object-fit: cover) or (object-fit: cover)) {
	.App__3st-slide video {
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		-o-object-fit: cover;
		   object-fit: cover;
	}
}

@-webkit-keyframes App__3st-slide_skill{
  from {   opacity: 0;
     margin-left: -80%;}
  to {   opacity: 1;  
    margin-left: 0%;}
}

@keyframes App__3st-slide_skill{
  from {   opacity: 0;
     margin-left: -80%;}
  to {   opacity: 1;  
    margin-left: 0%;}
}




